<template>
  <div class="panel" :style="`width:${currentwidth}`">
    <div class="panel-head" v-if="$slots.head">
      <slot name="head" />
    </div>
    <div class="panel-container">
      <slot name="content" />
    </div>
  </div>
</template>

<script>
export default {
  props: ["width"],
  computed: {
    currentwidth() {
      const { width } = this;
      return width * 1 ? width + "px" : width;
    },
  },
};
</script>

<style lang='less'>
.panel {
  border-radius: 5px;
  overflow: hidden;
  width: 90%;
  margin: 0 auto;
  margin-top: 20px;
  .panel-head {
    padding: 10px;
    background-color: #f6f6f6;
  }
  .panel-container {
    background-color: #fff;
    min-height: 100px;
  }
}
</style>