<template>
  <div :class="['reply', { active: mostReplyUpId === reply.id }]">
    <router-link :to="`/user/${reply.author.loginname}`">
      <el-avatar
        shape="square"
        :size="30"
        :src="reply.author.avatar_url"
      ></el-avatar>
    </router-link>
    <div class="reply-info">
      <div>
        <span>{{ reply.author.loginname }}</span
        ><span>{{ index + 1 }}楼·</span><span>{{ reply.create_at }}</span>
      </div>
      <p v-html="reply.content"></p>
    </div>
    <div class="operation">
      <span @click="handleLike" :class="{ red: reply.is_uped }"
        >赞{{ reply.ups.length }}</span
      >
      <span v-if="userInfer">回</span>
    </div>
  </div>
</template>

<script>
import { replyLike } from "@/http/api";
export default {
  data() {
    return {
      red: false,
    };
  },
  props: ["reply", "index", "mostReplyUpId", "userInfer"],
  methods: {
    async handleLike() {
      const accesstoken = localStorage.getItem("accesstoken");
      if (accesstoken) {
        const res = await replyLike({ reply_id: this.reply.id, accesstoken });
        console.log(res);

        this.$emit("getTopic");
      } else {
        this.$message({
          message: "请登录",
        });
      }
    },
  },
};
</script>

<style lang="less" scoped>
.reply {
  &.active {
    background-color: aqua;
  }
  display: flex;
  align-items: center;
  padding: 10px 10px 30px;
  border-top: 1px solid #ccc;
  .el-avatar,
  .opertaion {
    flex-shrink: 0;
  }
  .reply-info {
    flex-grow: 1;
    margin: 0 10px 0 10px;
  }
  .reply:last-child {
  }
  .red {
    color: red;
  }
}
</style>