<template>
  <div class="topic" v-if="topic">
    <panel-container>
      <template #content>
        <div class="topic-content">
          <h2>
            {{ topic.title }}
          </h2>
          <p>
            <span>{{ topic.author.loginname }}</span>
            <template v-if="userInfer">
              <el-button
                v-if="!topic.is_collect"
                type="success"
                @click="collect"
                >收藏</el-button
              >
              <el-button
                v-if="topic.is_collect"
                type="default"
                @click="decollect"
                >取消收藏</el-button
              >
            </template>
          </p>
          <hr />
          <div class="topic-html" v-html="topic.content"></div>
        </div>
      </template>
    </panel-container>
    <panel-container v-if="topic.reply_count">
      <template #head>{{ topic.reply_count }}回复 </template>
      <template #content>
        <replies-item
          v-for="(reply, index) in topic.replies"
          :key="reply.id"
          :reply="reply"
          :index="index"
          :mostReplyUpId="mostReplyUpId"
          :userInfer="userInfer"
          @getTopic="getTopic"
        />
      </template>
    </panel-container>
    <panel-container v-if="userInfer">
      <template #head> 添加评论 </template>
      <template #content>
        <el-input type="textarea" v-model.trim="replytext"></el-input>
        <el-button @click="addReply">添加</el-button>
      </template>
    </panel-container>
  </div>
</template>

<script>
import PanelContainer from "@/components/PanelContainer.vue";
import {
  collectTopic,
  getTopic,
  deCollectTopic,
  addTopicReply,
} from "../http/api";
import RepliesItem from "@/components/RepliesItem.vue";
import { mapState } from "vuex";
export default {
  components: { PanelContainer, RepliesItem },
  props: ["id"],
  data() {
    return {
      topic: null,
      replytext: "",
    };
  },
  computed: {
    ...mapState(["userInfer"]),
    mostReplyUpId() {
      let id = "",
        num = 0;
      this.topic.replies.forEach((reply) => {
        if (reply.ups.length > num) {
          id = reply.id;
          num = reply.ups.length;
        }
      });
      return id;
    },
    accesstoken() {
      const accesstoken = localStorage.getItem("accesstoken");
      return accesstoken || "";
    },
  },
  async created() {
    this.getTopic();
  },
  methods: {
    async collect() {
      const { topic, accesstoken } = this;
      const res = await collectTopic({
        topic_id: topic.id,
        accesstoken,
      });
      console.log(res);
      this.topic.is_collect = true;
    },
    async decollect() {
      const { topic, accesstoken } = this;
      const res = await deCollectTopic({
        topic_id: topic.id,
        accesstoken,
      });
      console.log(res);
      this.topic.is_collect = false;
    },
    async getTopic() {
      let params = { id: this.id };

      if (this.accesstoken) {
        params.accesstoken = this.accesstoken;
      }
      const res = await getTopic(params);

      this.topic = res.data;
      console.log(res.data);
    },
    async addReply() {
      const { replytext } = this;
      if (replytext) {
        await addTopicReply({
          accesstoken: this.accesstoken,
          content: replytext,
          id: this.topic.id,
        });
        this.replytext = "";
        this.getTopic();
      }
    },
  },
};
</script>

<style lang="less" scoped>
.topic-content {
  padding: 10px;
}
/deep/ .topic-html img {
  width: 100%;
}
.topic-content p {
  display: flex;
  justify-content: space-between;
}
</style>