import VueRouter from "vue-router";
import Vue from "vue";
import HomeViews from "../views/HomeViews.vue"
import LoginView from '../views/LoginView.vue'
import MainView from '../views/MainView.vue'
import TopicView from '../views/TopicView.vue'
import TopicXiang from '../views/TopicXiang.vue'
import UserView from '../views/UserView.vue'
import NotFound from '../views/NotFound.vue'
Vue.use(VueRouter)

const routes = [
    {
        path: "/login",
        name: "login",
        component: LoginView
    },
    {
        path: "/",
        component: MainView,
        children: [
            {
                path: '/notfound',
                component: NotFound,


            },
            {
                path: "",

                component: HomeViews,
                children: [{
                    path: '',
                    name: 'home',
                    component: TopicView
                }, {
                    path: ':tab',

                    props: true,
                    component: TopicView
                }]
            }, {
                path: '/topic/:id',
                name: "topic",
                props: true,
                component: TopicXiang
            }, {
                path: '/user/:loginname',
                name: "user",
                props: true,
                component: UserView
            }
        ]
    },
]

const router = new VueRouter({
    mode: "hash",
    routes
})
export default router