import Vuex from "vuex"
import Vue from 'vue'
Vue.use(Vuex)
const store = new Vuex.Store({
    state: {
        userInfer: null

    },
    mutations: {
        getUserInfer(state, userInfer) {
            state.userInfer = userInfer
        }
    },
    actions: {
        getUserInfer({ commit }) {
            const userInfer = localStorage.getItem("userInfer")
            if (userInfer) {
                commit('getUserInfer', JSON.parse(userInfer))
            }
        }
    }

})
export default store