<template>
  <div class="login">
    <el-form
      :model="userInfer"
      status-icon
      :rules="rules"
      ref="userInfer"
      label-width="100px"
    >
      <el-form-item label="账号" prop="username">
        <el-input
          type="text"
          v-model="userInfer.username"
          autocomplete="off"
          placeholder="请输入账号"
        ></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input
          type="password"
          v-model="userInfer.password"
          autocomplete="off"
          placeholder="请输入密码"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('userInfer')"
          >提交</el-button
        >
        <el-button @click="resetForm('userInfer')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { login } from "../http/api";
export default {
  data() {
    return {
      userInfer: {
        username: "",
        password: "",
      },
      rules: {
        username: [
          {
            validator(rule, value, callback) {
              if (value === "") {
                callback("请输入用户名");
              } else {
                callback();
              }
            },
            message: "请输入用户名",
            trigger: "blur",
          },
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }],
      },
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          login({ accesstoken: this.userInfer.password })
            .then((res) => {
              this.$message({
                message: "登录成功",
                type: "success",
                duration: 1000,
              });
              console.log(res);
              const userInfer = {
                loginname: res.loginname,
                avatar_url: res.avatar_url,
                id: res.id,
              };
              localStorage.setItem("userInfer", JSON.stringify(userInfer));
              localStorage.setItem("accesstoken", this.userInfer.password);
              this.$store.commit("getUserInfer", userInfer);
              this.$router.push("/");
            })
            .catch(() => {
              this.$message({
                message: "密码有误()",
                type: "error",
              });
            });
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  },
};
</script>

<style lang="less">
.login {
  width: 100%;
  height: 100vh;
}
.login .el-form {
  width: 500px;
  margin: 100px auto;
}
</style>