<template>
  <div class="userview">
    <panel-container>
      <template #head>主页</template>
      <template #content>
        <div class="zhuye">
          <el-avatar
            shape="square"
            :size="30"
            :src="userDetail.avatar_url"
          ></el-avatar>
          <span>{{ userDetail.loginname }}</span>
          <span>{{ userDetail.score }}积分</span>
          <span>https://github.com/{{ userDetail.githubUsername }}</span>
        </div>
      </template>
    </panel-container>
    <panel-container>
      <template #head>最近创建的话题</template>
      <template #content>
        <!-- <replies-item> -->
        <!-- {{ item.title }} -->
        <!-- </replies-item> -->
        <!-- v-for="item in this.userDetail.recent_replies"
          :key="item.id" -->
        <div
          class="replys"
          v-for="item in userDetail.recent_replies"
          :key="item.create_at"
        >
          <el-avatar
            shape="square"
            :size="30"
            :src="userDetail.avatar_url"
          ></el-avatar>
          <span>{{ item.title }}</span>
          <p>
            <el-avatar
              shape="square"
              :size="30"
              :src="item.author.avatar_url"
            ></el-avatar>
            <span>{{ item.last_reply_at | time }}</span>
          </p>
        </div>
      </template>
    </panel-container>
    <panel-container>
      <template #head>最近参与的话题</template>
      <template #content>
        <div class="replyfa">
          <div
            class="replys"
            v-for="item in userDetail.recent_topics"
            :key="item.create_at"
          >
            <el-avatar
              shape="square"
              :size="30"
              :src="userDetail.avatar_url"
            ></el-avatar>
            <span>{{ item.title }}</span>

            <p>
              <el-avatar
                shape="square"
                :size="30"
                :src="item.author.avatar_url"
              ></el-avatar>
              <span>{{ item.last_reply_at | time }}</span>
            </p>
          </div>
        </div>
      </template>
    </panel-container>
  </div>
</template>

<script>
import { getUserDetail } from "@/http/api";
import PanelContainer from "@/components/PanelContainer.vue";

export default {
  components: { PanelContainer },
  props: ["loginname"],
  watch: {
    loginname: {
      async handler(value) {
        const res = await getUserDetail({
          loginname: value,
        });
        this.userDetail = res.data;
        console.log(this.userDetail);
      },
      immediate: true,
    },
  },
  data() {
    return {
      userDetail: "",
    };
  },
};
</script>

<style lang="less" scoped>
.replys {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 10px 30px;
  border-top: 1px solid #ccc;
  p {
    display: flex;
    align-items: center;
  }
}
.replyfa {
  max-height: 355px;
  overflow: hidden;
}
.zhuye {
  display: flex;
  flex-direction: column;
  padding-left: 10px;
  span {
    margin: 10px 0 10px 0;
  }
}
</style>